* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

body {
  overflow-x: hidden;
  font-weight: normal;
  line-height: 1.5;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  overflow-x: hidden;
}

ul,
ol {
  list-style: none;
}

.richtext ul {
  list-style: disc;
  padding-left: 20px;
}
.richtext ol {
  list-style: decimal;
  padding-left: 20px;
}

a {
  text-decoration: none;
}

/*a:active,
a:hover {
  outline: 0 !important;
}*/
h1,
h2,
h3,
h4,
h5,
h6 {
  hyphens: none;
  padding-bottom: 10px;
  line-height: 1.3;
  font-weight: bold;
  display: inline-block;
  vertical-align: top;
  margin: 0;
  margin-bottom: 45px;
}

h1 {
  font-size: 3.75rem;
}

h2 {
  font-size: 3.125rem;
}

h3 {
  font-size: 1.125rem;
}

h4 {
  font-size: 1.125rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1.125rem;
}

p {
  word-break: break-word;
  font-size: 1.125rem;
  padding-bottom: 1em;
}

span {
  word-break: break-word;
  font-size: 1.125rem;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 3.125rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.125rem;
  }
  h4 {
    font-size: 1.125rem;
  }
  h5 {
    font-size: 1.125rem;
  }
  h6 {
    font-size: 1.125rem;
  }
  p {
    font-size: 1.125rem;
  }
  span {
    font-size: 1.125rem;
  }
}
@media screen and (max-width: 480px) {
  h1 {
    font-size: 2.125rem;
  }
  h2 {
    font-size: 2.125rem;
  }
  h3 {
    font-size: 1.125rem;
  }
  h4 {
    font-size: 1.125rem;
  }
  h5 {
    font-size: 1.125rem;
  }
  h6 {
    font-size: 1.125rem;
  }
  p {
    font-size: 1.125rem;
  }
  span {
    font-size: 1.125rem;
  }
}
.sr-only,
.section-title-sr-only .section-title-container {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0 !important;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.page-container {
  padding-top: 80px;
  overflow-y: hidden;
}

.content-section.contentWidth .container {
  max-width: 846px;
}

.container, .section-title-container {
  --bs-gutter-x: .75rem;
  max-width: 1440px;
  position: relative;
  margin: 0 auto;
  width: 80%;
  padding-left: var(--bs-gutter-x, 0.75rem);
  padding-right: var(--bs-gutter-x, 0.75rem);
}
@media screen and (max-width: 480px) {
  .container, .section-title-container {
    width: 94%;
  }
}
.container .disabled, .section-title-container .disabled {
  pointer-events: none;
}
.container .scroll-to-top, .container .move-to-top, .section-title-container .scroll-to-top, .section-title-container .move-to-top {
  margin: 0 auto;
  display: block;
}
.container .scroll-to-top > i::before, .container .move-to-top > i::before, .section-title-container .scroll-to-top > i::before, .section-title-container .move-to-top > i::before {
  content: "\f281";
  colore: red;
}

.sidebar {
  width: 20%;
  float: left;
}

.content-section {
