// General Overlay Styles
.overlay {
    display: none;
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.9);
    z-index: 9900;

    &.opened {
        display: block;
    }

    .overlay-container {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }

    // Search Overlay
    &.image-overlay,
    &.search-overlay {
        .image-overlay--container,
        .search-overlay--container {
            .image-wrapper,
            .search-form-wrapper {
                position: absolute;
                top: 20%;
                left: 50%;
                width: 50%;
                transform: translateX(-50%);
            }

            .image-wrapper {
                img {
                    width: 100%;
                }

                p {
                    background-color: $white;
                }
            }

            .search-form-wrapper {
                input {
                    font-size: 1.875rem;
                    width: 100%;
                    padding: 15px;
                }
            }
        }
    }

    // Iframe Overlay
    &.iframe-overlay {
        .iframe-overlay--container {
            .iframe-wrapper {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                max-width: 960px;
                width: 100%;

                &--inner {
                    position: relative;
                    padding-top: 56.25%;

                    iframe {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }

    .btn-wrapper {
        position: absolute;
        top: 20px;
        right: 30px;

        .close-overlay {
            color: $white;
            font-size: 3.125rem;
            line-height: 1;
            background: transparent;
        }
    }
}

// Mobile Layout
@media screen and (max-width: $screen-md) {
    .overlay {
        &.iframe-overlay {
            .iframe-overlay--container {
                .iframe-wrapper {
                    width: 90%;
                }
            }
        }
    }
}

@media screen and (max-width: $screen-s) {
    .overlay {
        &.search-overlay {
            .search-overlay--container {
                .search-form-wrapper {
                    top: 40%;
                    width: calc(100% - 60px);
                    transform: translate(-50%, -40%);

                    input {
                        font-size: 1.5625rem;
                        padding: 10px;
                    }
                }
            }
        }

        .btn-wrapper {
            .close-overlay {
                font-size: 2.5rem;
            }
        }
    }
}

#leave-overlay {
    z-index: 9999999999999;
    position: absolute;

    > div.blur-overlay {
        z-index: 1000001;
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.9);
    }

    > div.leave-popup {
        z-index: 1000002;
        width: 100%;
        max-width: 1100px;
        min-width: 320px;
        padding: 10px;
        position: fixed;
        max-height: 100vh;
        margin-bottom: 0 !important;
        top: 240px;
        background-color: $white;

        > div.popup-text {
            padding: 50px;
        }

        > a.popup-close {
            position: absolute;
            top: 0;
            right: 0;
            display: block;
            width: 30px;
            height: 30px;
            text-align: center;
            line-height: 1.875;
        }
    }
}
