// Button Class
.button {
    border-radius: 3px;

    i {
        line-height: inherit;
        font-size: inherit !important;
        margin-left: -1em;
        left: auto;
        display: inline-block;
        opacity: 0;
        transition: 0.2s ease-out;
        font-family: "ETmodules" !important;
    }

    &:hover i {
        margin-left: 0.3em;
        left: auto;
        margin-left: 0.3em;
        opacity: 1;
        transition: 0.2s ease-in;
    }

    &-wrapper {
        // button-white
        &.white {
            &.show-wrapper {
                border: 2px solid $mainColor;

                p {
                    color: $mainColor;
                }
            }

            .button {
                background-color: $white;
                color: $mainColor;
                border: 2px solid $mainColor;

                &.shiny {
                    box-shadow: 0px 2px 18px 9px $mainColor;
                }
            }
        }

        // button-blue
        &.blue {
            &.show-wrapper {
                border: 2px solid $mainColor;

                p {
                    color: $mainColor;
                }
            }

            .button {
                color: $white;
                background-color: $mainColor;
                border: 2px solid $mainColor;

                &.shiny {
                    box-shadow: 0px 2px 18px 9px $mainColor;
                }
            }
        }

        // button-trans
        &.trans {
            &.show-wrapper {
                border: 2px solid $white;

                p {
                    color: $white;
                }
            }

            .button {
                color: $white;
                border: 2px solid $white;
                background-color: transparent;

                &.shiny {
                    box-shadow: 0px 2px 18px 12px $white;
                }
            }
        }

        // button-dark
        &.dark {
            &.show-wrapper {
                border: 2px solid $light-black;

                p {
                    color: $light-black;
                }
            }

            .button {
                color: $white;
                border: 2px solid $yellow;
                background-color: $dark-grey;
                font-weight: normal;
                box-shadow: 6px 6px 18px 0px $dark-grey;

                &:hover {
                    color: $light-black;
                    border: 2px solid $light-black;
                    background-color: transparent;
                    box-shadow: none;
                }
            }
        }

        // more-link
        &.more-link {
            .button::after {
                content: "";
                display: block;
                height: 5px;
                background: $secondColor;
                background: linear-gradient(to right, $secondColor 1%, $mainColor 100%);
            }

            .button.yellow::after {
                content: "";
                display: block;
                height: 5px;
                background: $yellow;
            }

            .button.black::after {
                content: "";
                display: block;
                height: 5px;
                background: $light-black;
            }

            .button.magenta::after {
                content: "";
                display: block;
                height: 5px;
                background: $magenta;
            }
        }
    }
}
