@media screen and (max-width: $screen-l) {
.accordion-item {

    &.onlyMobile {
      .collapse {
        &:not(.show) {
          display: none;
        }
      }

      .collapsing {
        height: 0;
        overflow: hidden;
        @include transition($transition-collapse);
      }
    }
}
}

.accordion-item:not(.onlyMobile) {
  .collapse {
    &:not(.show) {
      display: none;
    }
  }

  .collapsing {
    height: 0;
    overflow: hidden;
    @include transition($transition-collapse);
  }
}

.accordion-item {

  &.onlyMobile{
    @media screen and (min-width: ($screen-l + 1)) {
      .column-title {
        .accordion-header {
          border-bottom: unset;
          button {
            pointer-events: none;
            &:after {
              background-image: unset;
            }
          }
        }
      }
    }
  }

  &:first-of-type {
    .column-title {
      .accordion-header {
        padding-top: 10px;
      }
    }
  }

  .accordion-header {
    width: 100%;
    padding-bottom: 16px;
    padding-top: 16px;
    border-top: 1px solid #80d2df;

     button {
       &:hover {
         cursor: pointer;
       }

       img {
         max-height: 50px;
         margin-right: 10px;
       }
     }
  }

  .column-title {
    .accordion-header {
      padding-bottom: 0px;
      padding-top: 24px;

      border-top: unset;
      button {
        padding-bottom: 9px;
        border-bottom: 1px solid $white;
      }
    }
  }

  .accordion-body {
    background-color: $white;
    padding: 12px 12px 12px 12px;
  }
}


.accordion {
  .accordion-body {
    padding: 0px 12px 12px 12px;
  }
}




//
// Base styles
//

.accordion-button {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  text-align: left; // Reset button style
  background-color: transparent;
  overflow-anchor: none;
  font-weight: bold;

  &:not(.collapsed) {

    &::after {
      background-image: escape-svg(url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{shade-color($black, 10%)}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>"));
      transform: rotate(-180deg);
    }
  }

  // Accordion icon
  &::after {
    flex-shrink: 0;
    width: 1.25rem;
    height: 1.25rem;
    margin-left: auto;
    content: "";
    background-image: escape-svg(url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$black}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>"));
    background-repeat: no-repeat;
    background-size: 1.25rem;
    @include transition(transform .2s ease-in-out);
  }

  &:hover {
    z-index: 2;
  }

  &:focus {
    z-index: 3;
    border-color: $black;
    box-shadow: $black;
  }
}

.accordion-header {
  margin-bottom: 0;
}

.accordion-item {
  background-color: transparent;
}
