.stageheader {
    // Text or Image Stageheader
    &--text-image {
        .swiper {
            .swiper-wrapper {
                .swiper-slide {
                    .swiper-slide--container {
                        .swiper-slide--content {
                            position: absolute;
                            left: 50%;
                            top: 50%;
                            transform: translate(-50%, -50%);
                            padding: 0;
                            margin: 0;
                            height: auto;

                            &.text-align-left {
                                text-align: left;
                                transform: translate(0, -50%);
                                left: 5%;
                            }

                            &.text-align-right {
                                text-align: right;
                                transform: translate(0, -50%);
                                right: 5%;
                                left: unset;
                            }

                            .content-heading {
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    &.small {
        .stageheader--slider {
            .swiper {
                .swiper-wrapper {
                    .swiper-slide {
                        height: 360px;
                    }
                }
            }
        }
    }

    &--slider {
        .swiper {
            .swiper-wrapper {
                .swiper-slide {
                    height: 966px;

                    .swiper-slide--image-container {
                        position: relative;
                        margin-left: auto;
                        margin-right: auto;
                        max-width: 2500px;
                        width: 100%;
                        height: 100%;
                        overflow: hidden;

                        img {
                            position: absolute;
                            transform: translate(-50%, -50%);
                            left: 50%;
                            top: 50%;
                        }
                    }

                    &--container {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                    }

                    &--content {
                        position: relative;
                        margin: 200px auto 100px;
                        padding: 90px 25px 0;
                        height: 566px;
                        max-width: 480px;
                        text-align: center;
                    }


                    // Arrow button to scroll down the page
                    .scrolldown {
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 10px;
                        display: block;

                        &::after {
                            content: '\f119';
                            font-family: bootstrap-icons;
                            font-size: 5rem;
                        }
                    }
                }
            }
        }
    }
}
