.pagination {
    .pagi-holder {
        .item {
            &:hover {
                color: $white;
                background-position: center center;
                background-image: url("../img/hexagon-tuerkis.svg");
                background-repeat: no-repeat;
                background-size: 36px 40px;
            }

            &.active {
                background-image: url("../img/hexagon-tuerkis.svg");
                background-size: 36px 40px;
                color: $white;
                opacity: 1;
            }
        }
    }
}