// General Swiper Styles
.swiper {
    .swiper-wrapper {
        box-sizing: border-box;

        .swiper-slide {
            align-self: center;
        }
    }
}

// Custom Swiper Styles
.videoslider,
.dynamic-slider {
    .slider--container {
        position: relative;
    }

    &.has-navigation {
        .swiper {
            margin: 0 100px;

            @media screen and (max-width: $screen-s) {
                margin: 0;
            }
        }

        .swiper-button-next,
        .swiper-button-prev {
            top: calc(50% - 35px);

            @media screen and (max-width: $screen-s) {
                display: none;
            }

            &::after {
                font-family: bootstrap-icons;
                font-weight: 900;
                font-size: 2.25rem;
                color: $mainColor;
            }
        }

        .swiper-button-next {
            right: 30px;

            &::after {
                content: "\f285";
            }
        }

        .swiper-button-prev {
            left: 30px;

            &::after {
                content: "\f284";
            }
        }
    }

    .swiper-pagination {
      position: relative;
      top: 20px;
      margin-top: 18px;

      .swiper-pagination-bullet {
          margin: 0 4px;
          background-color: $black;
          height: 36px;
          width: 31px;
          border-radius: 0;
          opacity: 0.5;

          &.swiper-pagination-bullet-active {
              opacity: 1;
          }
      }
    }
}
