@property --num {
    syntax: "<integer>";
    initial-value: 0;
    inherits: false;
}

.infographic {
    .row {
        margin-left: auto;
        margin-right: auto;
            
        img {
            width: 100%;
        }

        .info-object {
            text-align: center;

            i {
                font-size: 6.25rem;
                display: block;
            }

            h3 {
                &.counter {
                    transition: --num 2s;
                    counter-set: num var(--num);
                
                    &::after {
                        content: counter(num);
                    }
                }
            }

            .circle {
                margin-left: auto;
                margin-right: auto;
                max-width: 225px;
                position: relative;

                p {
                    position: relative;
                    padding: 49% 0;
                    font-size: 2.5rem;
                    line-height: 0.1;
                    margin-bottom: 20px;

                    .percent-value {
                        //    --num: 100;
                        transition: --num 2s;
                        counter-set: num var(--num);
                    
                        &::after {
                            content: counter(num);
                        }
                    }
                }
                
                canvas {
                    position: absolute;
                    width: 100%;
                    height: auto;
                    top: 0;
                    left: 0;
                }
            }
        }
    }
}
