// General Input Styling
input {
    &:not([type="submit"]) {
        color: $black;
        background: $white;
        appearance: none;
        box-shadow: none;
        border: 0;
        color: inherit;
        font-size: inherit;
    }

    &.error {
        border: 1px solid $error-color;
    }

    &[type="search"] {
        box-sizing: border-box;
    }

    &[type="text"],
    &[type="email"],
    &[type="password"] {
        width: 100%;
        padding: 12px 15px;
        border: 1px solid $grey;

        &.captcha {
            max-width: 44px;
            padding: 12px 5px;
            text-align: center;
            vertical-align: top;
        }
    }

    &[type="radio"] {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        border-radius: 100%;
        border: 1px solid $grey;
        margin-right: 5px;

        &:checked {
            border: 1px solid $mainColor;

            &::before {
                content: "";
                position: absolute;
                left: 2px;
                top: 2px;
                display: inline-block;
                width: 14px;
                height: 14px;
                border-radius: 100%;
                background-color: $mainColor;
            }
        }
    }

    &[type="checkbox"] {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        border: 1px solid $grey;
        margin-right: 5px;

        &:checked {
            background-color: $mainColor;
            border: 1px solid $mainColor;

            &::before {
                content: "\f26e";
                position: absolute;
                left: -1px;
                top: -1px;
                display: inline-block;
                font-family: bootstrap-icons;
                color: $white;
                font-size: 1.25rem;
                line-height: 1;
            }
        }
    }

    &[type="number"] {
        width: 100%;
        padding: 12px 15px;
        border: 1px solid $form-grey;

        &::-webkit-inner-spin-button {
            opacity: 1;
        }
    }

    &[type="date"] {
        width: 100%;
        padding: 9px 15px;
        border: 1px solid $form-grey;
    }
}

// extra checkbox styles
.radio,
.checkbox {
    position: relative;
    padding-left: 30px;

    input[type="radio"],
    input[type="checkbox"] {
        position: absolute;
        top: 2px;
        left: 0;
    }

    label {
        display: inline-block;
        font-size: 1.125rem;
        visibility: visible;
    }
}

// Placeholder for input fields
::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: $black;
    opacity: 1;
    font-family: "Roboto", sans-serif;
    /* Firefox */
}
