// General Input Styling
input {

    &[type="radio"] {
        background-color: $form-grey;
    }


    &[type="number"] {
        background-color: $form-grey;
        border-radius: 4px;
        // -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    }

    &[type="date"] {
        background-color: $form-grey;
        border-radius: 4px;
        // -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    }
}