.blog-query {
    padding-top: 30px;

    .query-filter {
        ul {
            li {
                line-height: 2.875;
                font-size: 1rem;
                float: left;

                @media screen and (max-width: $screen-xs) {
                    width: 50%;
                }

                &.active {
                    a {
                        color: $white;
                        pointer-events: none;
                        background-color: $mainColor;
                    }
                }

                a {
                    display: block;
                    padding: 0 30px;

                    @media screen and (max-width: $screen-md) {
                        padding: 0 20px;
                    }

                    &:hover {
                        color: $white;
                        background-color: $mainColor;
                    }
                }
            }
        }
    }

    &--list {
        .query-filter {
            ul {
                box-shadow: none;
                background-color: transparent;

                li {
                    width: 100%;
                    display: inline-block;
                    border-bottom: 1px solid $grey;

                    a:hover {
                        background-color: $grey;
                        color: $black;
                    }

                    &.active {
                        font-weight: bold;
                    }
                }
            }
        }

        .teaser-collection {
            > article {
                h2 {
                    font-weight: 400;
                    font-size: 1.875rem;
                    text-transform: none;
                    margin: 0;
                    color: $mainColor;
                }

                p.author-info {
                    color: #666666;
                    padding: 0;

                    a {
                        color: $mainColor;
                    }
                }
            }

            h1.category-title {
                font-size: 3.75rem;
                font-weight: bold;
                text-transform: none;

                span {
                    font-weight: 400;
                    color: $blue;
                }
            }

            @media screen and (max-width: $screen-md) {
                h1.category-title {
                    font-size: 3.4375rem;
                }

                @media (max-width: $screen-s) {
                    h1.category-title {
                        font-size: 2.75rem;
                    }
                }
            }
        }
    }

    &--teaser {
        .query-filter {
            position: relative;
            top: -30px;

            ul {
                display: inline-block;
                background-color: $white;
                box-shadow: 0px 10px 35px 0px rgba(0, 0, 0, 0.45);
            }
        }

        .teaser-collection {
            .grid-item {
                padding: 20px;

                @media screen and (max-width: $screen-md) {
                    padding: 15px;

                    .teaser {
                        margin-bottom: 0;
                    }
                }

                @media screen and (max-width: $screen-s) {
                    padding: 20px 0;
                }
            }

            .teaser {
                .content {
                    padding: 15px 15px 64px;

                    h3 {
                        margin: 10px 0;
                    }

                    p {
                        font-size: 1rem;
                    }

                    .category {
                        position: absolute;
                        bottom: 32px;
                        left: 15px;
                    }
                }
            }
        }
    }
}
