.table {
    width: 100%;
    border-collapse: collapse;

    table,
    th,
    td {
        border: 1px solid $white;
    }

    &--alternate {
        thead {
            color: $white;
            font-weight: bold;
            background-color: $mainColor;

            .table--element {
                padding: 5px;
                font-size: 1.125rem;

                &__title,
                &__thema {
                    width: 40%;
                }

                &__date {
                    width: 20%;
                }
            }
        }

        tbody {
            tr:nth-child(odd) {
                background-color: $form-grey;
            }

            .table--element {
                padding: 5px;

                // &__title,
                // &__thema {
                // }
                &__date {
                    text-align: center;
                }
            }
        }
    }
}