// ==========================================================================
// Global Styles
// ==========================================================================

body {
    color: $dark-grey;
    font-family: "Roboto", sans-serif;
    font-weight: 500;

    &.has-bgcolor {
        background-color: $light-grey;
    }
}

a {
    color: $dark-grey;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    &.underlined {
        &::after {
            content: "";
            display: block;
            height: 8px;
            background: $secondColor;
            background: linear-gradient(to right, $secondColor 1%, $mainColor 100%);
        }

        &.white {
            color: $white;

            &::after {
                background: $white;
            }
        }

        &.black {
            color: $black;

            &::after {
                background: $black;
                background: linear-gradient(to right, $black 1%, $dark-grey 100%);
            }
        }

        &.yellow {
            &::after {
                background: $yellow;
                background: linear-gradient(to right, $yellow 1%, $yellow-light 100%);
            }
        }

        &.magenta {
            &::after {
                background: $magenta;
                background: linear-gradient(to right, $magenta 1%, $magenta-light 100%);
            }
        }
    }
}

h1 {
    text-transform: uppercase;
}

h2 {
    text-transform: uppercase;
}

// ==========================================================================
// Basic Style
// ==========================================================================

.content-section {
    .section-title {
        a {
            color: $mainColor;
        }
    }

    &.yellow-bg {
        background: $yellow;
    }

    &.magenta-bg {
        background: rgba(172, 20, 124, 0.16);
    }

    &.grey-gradient-bg {
        background-repeat: repeat;
        background-image: linear-gradient(180deg, $light-grey 0%, rgba(242, 242, 242, 0.7) 100%);
    }

    &.blue-gradient-bg {
        background-repeat: repeat;
        background-image: linear-gradient(90deg, rgba(0, 165, 190, 0.99) 0%, rgba(0, 100, 175, 0.8) 100%);

        a {
            color: $white;
        }

        .dynamic-slider {
            &.has-navigation {
                .swiper-button-prev,
                .swiper-button-next {
                    &::after {
                        color: $white;
                    }
                }
            }

            .swiper-pagination {
                .swiper-pagination-bullet {
                    background: url("../img/hexagon-weiss-start.svg");
                    background-size: cover;
                }
            }
        }
    }

    &.fixed-bg {
        background-attachment: fixed;
        background-repeat: repeat;
        background-position: center center;
        background-size: cover;

        &.bg-waben {
            background-image: url("../img/bg_octis.svg");

            &.grey-gradient-bg {
                background-image: linear-gradient(180deg, $light-grey 0%, rgba(242, 242, 242, 0.7) 100%),
                url("../img/bg_octis.svg") !important;
            }

            &.blue-gradient-bg {
                background-image: linear-gradient(90deg, rgba(0, 165, 190, 0.99) 0%, rgba(0, 100, 175, 0.8) 100%),
                url("../img/bg_octis.svg") !important;
            }
        }
    }
}

.row > div > div.has-border {
    background-color: $white;
    padding: 25px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
    margin-bottom: 25px;
}
