.navigation.link {
  position: relative;
  display: block;
  margin-top: 30px;
  margin-left: -12px;
  margin-right: -12px;


  .back-to-radar {
    position: absolute;
    top: -50px;
    left: 0;
    padding-left: 20px;
  }

  .navigation-head {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
}

.navigation.box {
  position: relative;
  z-index: 10;

  .back-to-radar {
    position: absolute;
    top: -20px;
    left: 0;

    @media screen and (max-width: $screen-md) {
      top: 0;
    }
  }

  .navigation-wrapper {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media screen and (min-width: $container-large-screen-size) {
      left: 50%;
      transform: translatex(-50%);
      width: calc(100% + 472px);
    }

    .radar-box {
      min-width: 192px;
      opacity: 0.6;
    }

    a.radar-box {
      position: relative;
      top: 30px;
      width: 192px;
      height: 192px;
      // margin: 0 122px;

      @media screen and (min-width: $container-large-screen-size) {
        top: 250px;
      }

      &:first-of-type {
        @media screen and (max-width: $screen-s) {
          position: absolute;
          left: calc(-26px + (50% - 96px));
          // left: -24px;
          transform: translateX(-100%);
        }
      }

      &:last-of-type {
        @media screen and (max-width: $screen-s) {
          position: absolute;
          right: calc(-26px + (50% - 96px));
          transform: translateX(100%);
        }
      }
    }
  }
}

.radar-box--navigation {
  position: relative;
  width: calc(100% - 192px - 192px - 26px - 26px);
  max-width: 856px;
  height: 192px;
  margin: 40px auto 70px;

  @media screen and (min-width: $container-large-screen-size) {
    margin-bottom: 0px;
  }

  @media screen and (max-width: $screen-md) {
    //height: 192px;
    width: 192px;
  }
}
