.quote {
    background-repeat: repeat;
    background-image: linear-gradient(90deg, rgba(0, 165, 190, 0.99) 0%, rgba(0, 100, 175, 0.8) 100%);

    &-wrapper {
        padding: 10px 100px;
        text-align: center;
        color: $white;

        .text-wrapper {
            margin-top: 50px;

            &::before {
                content: "";
                float: left;
                margin-left: -95px !important;
                margin-top: -55px !important;
                background-image: url("../img/zitat-links.svg") !important;
                background-size: 100% 100% !important;
                background-repeat: no-repeat;
                height: 70px !important;
                width: 75px !important;
            }

            &::after {
                content: "";
                margin-top: -25px !important;
                left: 95px !important;
                position: relative;
                float: right;
                background-image: url("../img/zitat.svg") !important;
                background-size: 100% 100% !important;
                background-repeat: no-repeat;
                height: 70px !important;
                width: 75px !important;
            }

            h2 {
                font-size: 1.75rem;
                font-weight: bold;
                margin-bottom: 0;
                padding-top: 10px;

                a {
                    color: $white;
                    text-decoration: underline;
                }
            }

            p {
                font-size: 1.125rem;
                line-height: 1.5;
                font-weight: 400;
            }
        }
    }
}
