// ==========================================================================
// Colors
// ==========================================================================

$grey: #CCCCCC !default;
$light-grey: #F2F2F2 !default;
$form-grey: #EEEEEE !default;
$mid-grey: #777777 !default;
$dark-grey: #363636 !default;
$light-black: #181818 !default;
$black: #000000 !default;
$white: #FFFFFF !default;
$strong-turquoise: #00A5BE !default;
$blue: #1462AC !default;
$yellow: #FFCC00 !default;
$yellow-light: #FFF5CE !default;
$magenta: #AD157C !default;
$magenta-light: #AA88A0 !default;
$error-color: #FF0000 !default;
$erinnerungszeichen-dark-blue: #535F92 !default;

// ==========================================================================
// Font Sizes
// ==========================================================================

$screen-l-fontsize-h1: 3.75rem !default;
$screen-l-fontsize-h2: 3.125rem !default;
$screen-l-fontsize-h3: 1.125rem !default;
$screen-l-fontsize-h4: 1.125rem !default;
$screen-l-fontsize-h5: 1.125rem !default;
$screen-l-fontsize-h6: 1.125rem !default;
$screen-l-fontsize-p: 1.125rem !default;
$screen-l-fontsize-span: 1.125rem !default;

$screen-s-fontsize-h1: 3.125rem !default;
$screen-s-fontsize-h2: 2.5rem !default;
$screen-s-fontsize-h3: 1.125rem !default;
$screen-s-fontsize-h4: 1.125rem !default;
$screen-s-fontsize-h5: 1.125rem !default;
$screen-s-fontsize-h6: 1.125rem !default;
$screen-s-fontsize-p: 1.125rem !default;
$screen-s-fontsize-span: 1.125rem !default;

$screen-xs-fontsize-h1: 2.125rem !default;
$screen-xs-fontsize-h2: 2.125rem !default;
$screen-xs-fontsize-h3: 1.125rem !default;
$screen-xs-fontsize-h4: 1.125rem !default;
$screen-xs-fontsize-h5: 1.125rem !default;
$screen-xs-fontsize-h6: 1.125rem !default;
$screen-xs-fontsize-p: 1.125rem !default;
$screen-xs-fontsize-span: 1.125rem !default;

// ==========================================================================
// Screen Sizes
// ==========================================================================

$screen-xl: 1400px !default;
$screen-l: 1368px !default;
$screen-md: 1024px !default;
$screen-s: 768px !default;
$screen-xs: 480px !default;

// Boostrap Breakpoints


$grid-breakpoints: (
        xs: 0,
        sm: $screen-s,
        md: $screen-md,
        lg: $screen-l,
        xl: $screen-xl
);

// ==========================================================================
// Container Sizes
// ==========================================================================

$container-large: 1440px !default;
$container-small: 846px !default;

$container-large-screen-size: 1800px !default;

// ==========================================================================
// Theme definitions
// ==========================================================================

$mainColor: $dark-grey !default;
$secondColor: $grey !default;
$highlightColor: #2790ee !default;
