.videoslider {
    position: relative;
    
    .swiper {
        margin: 130px 90px 90px;

        .swiper-slide {
            .video-preview-wrapper {
                a {
                    position: relative;
                    display: block;

                    &:hover {
                        .play-icon {
                            opacity: 1;
                        }
                    }

                    .preview-img {
                        width: 100%;
                    }

                    .play-icon {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        opacity: 0;
                        width: 60px;
                        transition: opacity 0.5s;
                    }
                }
            }
        }
    }
}

// Mobile Layout

@media screen and (max-width: $screen-md) {
    .videoslider {
        .swiper {
            margin: 80px 0;
        }
    }
}

@media screen and (max-width: $screen-s) {
    .videoslider {
        .swiper {
            margin: 80px 60px;
        }
    }
}
