.header {
    background-color: $light-grey;

    &--navigation {
        nav {
            .nav-menu {
                .menu-item {
                    ul {
                        background-color: $light-grey;
                    }

                    &:hover {
                        background-color: $white;
                    }

                    &.has-submenu > a > i::before {
                        content: "\f282";
                        //font-family: bootstrap-icons; set via bi class
                    }

                    &.open {
                        background-color: $white;
                    }

                    &.current-menu-item {
                        > a {
                            &::after {
                                content: "";
                                display: block;
                                position: absolute;
                                width: 100%;
                                left: 0;
                                height: 4px;
                                background: $secondColor;
                                background: linear-gradient(to right, $secondColor 1%, $mainColor 100%);
                            }
                        }
                    }

                    a {
                        &:hover {
                            &::after {
                                content: "";
                                display: block;
                                position: absolute;
                                width: 100%;
                                left: 0;
                                height: 4px;
                                background: $secondColor;
                                background: linear-gradient(to right, $secondColor 1%, $mainColor 100%);
                            }
                        }
                    }
                }
            }
        }

        // Search Menu
        .search {
            .inline-search {
                span {
                    color: $dark-grey;
                }
            }

            .inline-login {
                span {
                    color: $dark-grey;
                }
            }
        }
    }
}

// Mobile Layout
@media screen and (max-width: $screen-l) {
    .header {
        &--navigation {
            .mobile-nav {
                &.opened {
                    .mobile-menu {
                        border-top: 3px solid $strong-turquoise;
                    }
                }

                .mobile-menu {
                    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
                    background-color: $light-grey;

                    .menu-item {
                        &.active {
                            background-color: $mainColor;

                            a {
                                color: $white;
                            }
                        }

                        &:last-child {
                            a {
                                border-bottom: 0;
                            }
                        }

                        a {
                            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
                        }
                    }
                }

                .mobile-menu-toggle {
                    box-shadow: 0 0px 2px rgba(0, 0, 0, 0.1);
                    border: 1px solid $grey;
                    background-color: $white;
                }
            }
        }
    }
}
