.pagination {
    .pagi-holder {
        text-align: center;
        font-size: 1.125rem;
        font-weight: 400;

        ul {
            li {
                display: inline-block;
                margin: 0 2px;

                @media screen and (max-width: $screen-s) {
                    margin: 0;
                }
            }
        }

        a {
            width: 36px;
            height: 40px;
            margin: 0;
            line-height: 2.5;
            display: inline-block;
        }

        span {
            height: 40px;
            display: inline-block;
            line-height: 2.5;
            margin: 0;
        }

        .first,
        .last {
            color: $black;

            &:hover {
                opacity: 0.7;
            }

            &.disabled {
                pointer-events: none;
                opacity: 0.5;
            }
        }

        .item {
            &:hover {
                opacity: 0.7;
            }

            &.active {
                color: $black;
            }
        }
    }
}
