// General Overlay Styles
.overlay {
    // Search Overlay
    &.image-overlay,
    &.search-overlay {
        .image-overlay--container,
        .search-overlay--container {
            .image-wrapper {
                p {
                    padding-bottom: 0;
                    background-color: $black;
                    color: $white;
                    text-align: center;
                    margin-top: -6px;
                }
            }

            .search-form-wrapper {
                input {
                    border: 4px solid $strong-turquoise;
                }
            }
        }
    }
}

#leave-overlay {
    > div.blur-overlay {
        backdrop-filter: saturate(180%) blur(5px);
        background: rgba(0, 0, 0, 0.55);
    }

    > div.leave-popup {
        background-image: linear-gradient(90deg, #2F7EBB 0%, #2F7EBB 100%);

        > div.popup-text {
            color: $white;

            h2::after {
                content: "";
                width: auto;
                display: block !important;
                height: 8px;
                background: $white;
            }

            a {
                color: $white;
                text-decoration: underline;
                font-weight: bold;
            }
        }

        > a.popup-close {
            background: $black;
            color: $white;
        }
    }
}
