.header {
  position: fixed;
  top: 0px;
  width: 100%;
  margin: 0 auto;
  height: 80px;
  z-index: 9800; // Header Logo

  &--logo {
    width: 240px;
    float: left;

    a {
      display: inline-block;
      position: relative;
      height: 80px;
    }

    img {
      top: 2px;
      margin-left: -20px;
      max-height: 108px;
      z-index: 9800;
    }
  }

  // Desktop & Mobile Navigation
  &--navigation {
    width: calc(100% - 240px);
    display: flex;
    justify-content: flex-end;

    .home-icon {
      position: relative;
      top: 1px;
      height: 15px;
    }

    // Desktop Navigation
    nav {
      width: 70%;
      float: left;

      .nav-menu {
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;

        .menu-item {
          display: inline-flex;
          font-size: 1rem;
          position: relative;
          flex-grow: 1;
          height: 100%;
          align-items: center;
          padding: 1em 0;

          ul ul {
            position: absolute;
            left:100%;
            top:0px;
          }

          ul {
            display: none;
            position: absolute;
            top: 100%;
            width: 100%;
            text-align: center;

            li {
              display: block;
            }
          }

          &.open > ul {
              display: block;
          }

          a {
            position: relative;
            margin: 0 auto;
          }
        }
      }
    }

    // Search Menu
    .search {
      width: 30%;
      text-align: right;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      .inline-search {
        display: inline-flex;
        margin-right: 25px;
        padding-bottom: 6px;

        i:before {
          content: "\F52A";
          //font-family: bootstrap-icons; set via bi class
          font-weight: 100;
          font-size: 1.5rem;
        }

        span {
          font-weight: 100;
          font-size: 2.0625rem;
        }
      }

      .inline-login {
        display: inline-flex;
        margin-right: 25px;

        span {
          font-weight: 100;
          font-size: 1.375rem;
          line-height: 1.2;

          &.bi {
            line-height: 1;
          }
        }
      }

      .logo-lhm {
        display: inline-flex;

        img {
          width: 200px;
        }
      }
    }

    // Mobile Navigation
    .mobile-nav {
      display: none;

      .menu-item {
        font-size: 1rem;
        position: relative;

        ul {
          display: none;
          position: relative;
          top: 100%;
          width: 100%;

          li {
            display: block;
          }
        }

        &.open > ul {
            display: block;
          }


        a {
          position: relative;
          padding: 28px 0;
          margin: 0 auto;
        }
      }

      .select-page {
        visibility: hidden;
        opacity: 0;
        font-size: 0;
      }
    }

    &.alwaysBurger {
      .mobile-nav {
        display: block;
        margin-right: 25px;

        &.opened {
          .mobile-menu {
            display: block;
            max-height: 1000px;
            padding: 5%;
          }
        }
        .mobile-menu {
          display: none;
          max-height: 0;
          padding: 0 5%;
          overflow: hidden;
          position: absolute;
          top: 80px;
          z-index: 9700;
          left: 0;
          width: 100%;
          transition: max-height 0.5s ease-in-out, padding 0.5s ease-in-out;

          .menu-item a {
            font-size: 1.125rem;
            line-height: 1.2;
            font-weight: 400;
            display: block;
            padding: 10px;
          }
        }

        &--container {
          height: 80px;
          display: flex;
          align-items: center;
        }

        .mobile-menu-toggle {
          height: 40px;
          width: 42px;
          display: inline-flex;
          position: relative;
          cursor: pointer;

          span {
            display: block;
            position: absolute;
            width: 20px;
            height: 2px;
            left: 10px;
            background-color: $mainColor;

            &.first {
              top: 13px;
            }

            &.second {
              top: 19px;
            }

            &.third {
              top: 25px;
            }
          }
        }
      }
      nav {
        display: none;
      }
    }
  }
}

// Mobile Layout
@media screen and (max-width: $screen-l) {
  .header {
    &--navigation {
      nav {
        display: none;
      }

      .search {
        min-width: 255px;
      }

      .mobile-nav {
        display: block;
        margin-right: 25px;

        &.opened {
          .mobile-menu {
            display: block;
            max-height: 1000px;
            padding: 5%;
          }
        }

        .mobile-menu {
          display: none;
          max-height: 0;
          padding: 0 5%;
          overflow: hidden;
          position: absolute;
          top: 80px;
          z-index: 9700;
          left: 0;
          width: 100%;
          transition: max-height 0.5s ease-in-out, padding 0.5s ease-in-out;

          .menu-item a {
              font-size: 1.125rem;
              line-height: 1.2;
              font-weight: 400;
              display: block;
              padding: 10px;
          }
        }

        &--container {
          height: 80px;
          display: flex;
          align-items: center;
        }

        .mobile-menu-toggle {
          height: 40px;
          width: 42px;
          display: inline-flex;
          position: relative;
          cursor: pointer;

          span {
            display: block;
            position: absolute;
            width: 22px;
            height: 3px;
            left: 10px;
            background-color: $mainColor;

            &.first {
              top: 10px;
            }

            &.second {
              top: 18px;
            }

            &.third {
              top: 26px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: $screen-s) {
  .header {
    position: absolute;

    &--navigation {
      .search {
        width: auto;
        min-width: auto;

        .logo-lhm {
          img {
            width: auto;
            max-height: 40px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: $screen-xs) {
  .header {
    &--container {
      width: 100%;
      padding: 0 30px;
    }

    &--logo {
      width: 120px;
    }

    &--navigation {
      width: calc(100% - 120px);

      .mobile-nav {
        &.opened {
          .mobile-menu {
            padding: 30px;
          }
        }

        .mobile-menu {
          padding: 0 30px;
        }
      }
    }
  }
}
