// Button Element
button {
    appearance: none;
    box-shadow: none;
    border: 0;
    color: inherit;
    font-size: inherit;
    text-align: inherit;
}

// Button Class
.button {
    display: inline-block;
    font-size: 1.25rem;
    padding: 6px 20px;
    font-weight: bold;

    &-wrapper {
        text-align: center;
        padding: 40px 20px;

        &.show-wrapper {
            p {
                font-size: 1.25rem;
                font-weight: bold;
            }
        }

        &:not(.more-link) {
            .button {
                border: 2px solid $mainColor;
            }
        }
    }
}
