.infographic {
    .row {
        max-width: 1080px;

        .info-object {
            color: $white;

            h3 {
                margin-bottom: 0;
                padding-bottom: 10px;
                font-size: 2.0625rem;
                line-height: 1.5;
                font-weight: normal;
            }

            p {
                font-weight: 300;
                font-size: 0.9375rem;
            }

            .circle {
                h3 {
                    font-size: 1.3125rem;
                    line-height: 1.3;
                }
            }
        }
    }

    &.skyline {
        .row {
            background: linear-gradient(90deg, $secondColor 0%, $mainColor 100%);

            &:first-child {
                background-image: none;

                div {
                    padding-left: 0;
                    padding-right: 0;
                }

                &.has-image {
                    margin-bottom: -7px;
                }
            }
        }
    }
}
