.breadcrumb-component {
	// background-color: $gray-light;

	@include media-breakpoint-up(md) {
		padding: 0 rem(48);
	}

	.container,
	.container-fluid {
		display: flex;
	}
}

// Set negative margin, if breadcrumb is in breadcrumb container
.breadcrumb-component {
	.breadcrumb {
		margin-left: -(rem(9));
	}
}

.breadcrumb {
	padding: rem(8) 0;
	font-family: "Roboto Condensed", sans-serif;
	font-size: rem(16);
	line-height: 1.75;

	@include media-breakpoint-down(md) {
		display: none;
	}

	@include media-breakpoint-up(md) {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		padding: rem(14) 0 rem(14) 0;
	}

	&__list {
		display: flex;
		align-items: center;
		padding: 0;
		margin: 0;
		list-style: none;
	}

	&__list-item {
		display: flex;
		align-items: center;
	}

	&__list-item-current,
	&__list-item-link {
		padding: 0 rem(9);
	}

	&__list-item-link {
		color: $mainColor;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__list-item-current {
		font-size: rem(16);
		color: $digital-grey;
	}

	.icon-home {
		width: rem(16);
		height: rem(16);
		fill: $mainColor;
	}

	.breadcrumb__separator {
		width: rem(6);
		height: rem(10);
		display: flex;
		justify-content: center;
		align-items: center;
		fill: $digital-grey;
	}
}

.breadcrumb-go-back {
	display: flex;
	align-items: center;
	padding: rem(8) 0;

	&__link {
		padding: 0 0 0 rem(6);
		font-family: "Roboto Condensed", sans-serif;
		font-size: rem(14);
		line-height: 1.6;
		color: $mainColor;
	}

	.icon {
		width: rem(6);
		height: rem(10);
		fill: $digital-grey;
		transform: rotate(180deg);
		margin-right: 10px;
	}

	@include media-breakpoint-up(md) {
		display: none;
	}
}
