.blog-query {
    position: relative;

    &.loading {
        .teaser-collection {
            opacity: 0.1;
        }

        .loading-screen {
            display: block;
        }
    }

    .loading-screen {
        display: none;
        position: absolute;
        top: 90px;
        left: 0;
        width: 100%;
        height: calc(100% - 90px);
        z-index: 99;
    }

    &--list {
        .query-filter {
            float: left;
            width: 20%;
            padding-right: 30px;
            padding-bottom: 30px;

            @media screen and (max-width: $screen-md) {
                clear: both;
                width: 100%;
                padding-right: 0;
            }
        }

        .teaser-collection {
            float: left;
            width: 80%;

            > article {
                margin-bottom: 60px;
            }

            @media screen and (max-width: $screen-md) {
                clear: both;
                width: 100%;
            }
        }
    }

    &--teaser {
        .teaser-collection {
            .grid-sizer,
            .grid-item {
                width: 33.33%;

                @media screen and (max-width: $screen-md) {
                    width: 50%;
                }

                @media screen and (max-width: $screen-s) {
                    width: 100%;
                }
            }
        }
    }
}