.sidebar-nav {
    position: fixed;
    top: 50%;
    right: 40px;
    z-index: 9999;
    padding: 10px 20px;

    @media screen and (max-width: $screen-s) {
        display: none;
    }

    ul {
        li {
            display: block;
            margin: 10px 0;

            a {
                position: relative;
                display: block;
                margin: 0 auto;
                background-color: $black;
                height: 30px;
                width: 26px;
                opacity: 0.2;
                transition: opacity 0.3s;

                span {
                    display: block;
                    position: absolute;
                    top: 50%;
                    right: 60px;
                    transform: translateY(-50%);
                    padding: 3px 15px;
                    white-space: nowrap;
                    opacity: 0;
                }

                &:hover {
                    opacity: 1;

                    span {
                        opacity: 1;
                    }
                }
            }
        }
    }
}