.feedback {
    &.has-voted {
        .rating-wrapper {
            .overall-rating {
                display: none;
            }

            .after-rating {
                display: block;
            }
        }        
    }

    .rating-wrapper {
        .personal-rating {
            position: relative;
            display: inline-block;
            vertical-align: text-top;
            width: 160px;
            height: 32px;
            background-image: url("../img/star_grey.svg");
            background-size: 32px;
            background-repeat: repeat-x;

            span {
                position: absolute;
                width: calc(100% / 5);
                height: 32px;
                cursor: pointer;
                z-index: 1;

                &:first-of-type {
                    left: 0;
                }

                &:nth-of-type(2) {
                    left: calc(100% / 5);
                }

                &:nth-of-type(3) {
                    left: calc((100% / 5) * 2);
                }

                &:nth-of-type(4) {
                    left: calc((100% / 5) * 3);
                }

                &:nth-of-type(5) {
                    left: calc((100% / 5) * 4);
                }
            }

            .stars {
                position: absolute;
                left: 0;
                top: 0;
                width: 0;
                height: 32px;
                background-image: url("../img/star_blue.svg");
                background-repeat: repeat-x;
                background-size: 32px;
            }
        }

        .overall-rating {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin-left: 20px;
            
            i {
                font-size: 2.5rem;
                cursor: pointer;
            }
        }

        .after-rating {
            display: none;
            margin-top: 20px;
        }
    }
}