// ==========================================================================
// Style resets
// ==========================================================================

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

// ==========================================================================
// Global Styles
// ==========================================================================

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

body {
  overflow-x: hidden;
  font-weight: normal;
  line-height: 1.5;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  overflow-x: hidden;
}

ul,
ol {
  list-style: none;
}

.richtext {
  ul {
    list-style: disc;
    padding-left: 20px;
  }


  ol {
    list-style: decimal;
    padding-left: 20px;
  }
}

a {
  text-decoration: none;
}

/*a:active,
a:hover {
  outline: 0 !important;
}*/

h1,
h2,
h3,
h4,
h5,
h6 {
  hyphens: none;

  padding-bottom: 10px;
  line-height: 1.3;
  font-weight: bold;
  display: inline-block;
  vertical-align: top;
  margin: 0;
  margin-bottom: 45px;
}

h1 {
  font-size: $screen-l-fontsize-h1;
}

h2 {
  font-size: $screen-l-fontsize-h2;
}

h3 {
  font-size: $screen-l-fontsize-h3;
}

h4 {
  font-size: $screen-l-fontsize-h4;
}

h5 {
  font-size: $screen-l-fontsize-h5;
}

h6 {
  font-size: $screen-l-fontsize-h6;
}

p {
  word-break: break-word;
  font-size: $screen-l-fontsize-p;
  padding-bottom: 1em;
}

span {
  word-break: break-word;
  font-size: $screen-l-fontsize-span;
}

@media screen and (max-width: $screen-s) {
  h1 {
    font-size: $screen-s-fontsize-h1;
  }

  h2 {
    font-size: $screen-s-fontsize-h2;
  }

  h3 {
    font-size: $screen-s-fontsize-h3;
  }

  h4 {
    font-size: $screen-s-fontsize-h4;
  }

  h5 {
    font-size: $screen-s-fontsize-h5;
  }

  h6 {
    font-size: $screen-s-fontsize-h6;
  }

  p {
    font-size: $screen-s-fontsize-p;
  }

  span {
    font-size: $screen-s-fontsize-span;
  }
}

@media screen and (max-width: $screen-xs) {
  h1 {
    font-size: $screen-xs-fontsize-h1;
  }

  h2 {
    font-size: $screen-xs-fontsize-h2;
  }

  h3 {
    font-size: $screen-xs-fontsize-h3;
  }

  h4 {
    font-size: $screen-xs-fontsize-h4;
  }

  h5 {
    font-size: $screen-xs-fontsize-h5;
  }

  h6 {
    font-size: $screen-xs-fontsize-h6;
  }

  p {
    font-size: $screen-xs-fontsize-p;
  }

  span {
    font-size: $screen-xs-fontsize-span;
  }
}

.sr-only,
.section-title-sr-only .section-title-container {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0!important;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

// ==========================================================================
// Basic Style
// ==========================================================================

.page-container {
  padding-top: 80px;
  overflow-y: hidden;
}

.content-section.contentWidth {
  .container {
    max-width: $container-small;
  }
}

.container, .section-title-container {
  --bs-gutter-x: .75rem;
  max-width: $container-large;
  position: relative;
  margin: 0 auto;
  width: 80%;
  padding-left: var(--bs-gutter-x,.75rem);
  padding-right: var(--bs-gutter-x,.75rem);

  @media screen and (max-width: $screen-xs) {
    width: 94%;
  }

  .disabled {
    pointer-events: none;
  }

  .scroll-to-top, .move-to-top {

    margin: 0 auto;
    display: block;

    & > i::before {
      content: "\f281";
      //font-family: bootstrap-icons; set via bi class
      colore: red;
    }
  }
}

.sidebar {
  width: 20%;
  float: left;
}

.content-section {
  position: relative;
  padding: 70px 0;

  @media screen and (max-width: $screen-md) {
    padding: 50px 0;
  }

  &.distance {
    &-top {
      &-biggest {
        padding-top: 250px;
      }
      &-big {
        padding-top: 100px;
      }
      &-medium {
        padding-top: 25px;
      }
      &-small {
        padding-top: 10px;
      }
      &-none {
        padding-top: 0px;
      }
    }
    &-bottom {
      &-biggest {
        padding-bottom: 250px;
      }
      &-big {
        padding-bottom: 100px;
      }
      &-medium {
        padding-bottom: 25px;
      }
      &-small {
        padding-bottom: 10px;
      }
      &-none {
        padding-bottom: 0px;
      }
    }
  }

  .section-title {
    span {
      display: block;
    }

    p {
      margin-bottom: 45px;
    }
  }

  &.white-bg {
    background: $white;
  }

  &.dark-bg {
    background: $dark-grey;

    color: $white;

    .teaser {
      .richtext {
        a {
          color: $white;
        }
      }
    }
  }

  .container {
    @media screen and (max-width: $screen-l) {
      &.move-to-top-up {
        top: -180px;
        z-index: 10;

        .move-to-top {
          & > i::before {
            content: "\f27e";
          }
        }
      }
    }
  }
}

.error-msg {
  color: $error-color;
}

.content-centered {
  text-align: center;
}

.skip-link,
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  line-height: normal;
  font-weight: 600;
  -webkit-font-smoothing: subpixel-antialiased;
}

.skip-link:focus {
  background: #f1f1f1;
  color: #00547A;
  box-shadow: 0 0 2px 2px rgb(0 0 0 / 60%);
  clip: auto !important;
  display: block;
  height: auto;
  left: 5px;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 7px;
  width: auto;
  z-index: 1000000;

  position: absolute !important;
  overflow: hidden;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  line-height: normal;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 1em;
  font-weight: 600;
  -webkit-font-smoothing: subpixel-antialiased;

  outline: #2ea3f2 solid 2px;
  transition: none !important;
}

.screen-reader-text {
  font-size: 1em;
}

.bg-image {
  position: absolute;
  height: 100%;
  max-height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  object-fit: cover;

  &.focus-center {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &.focus-right {
    right: 0;
    left: auto;
  }

  &.fullscreen {
    min-width: 100%;
    min-height: 100%;
  }

  &.fullwidth {
    min-width: 100%;
    height: auto;

    &[src$=".svg"] {
      height: 100%;
    }
  }

  &.fullheight {
    min-height: 100%;
    width: auto;
  }
}

.page-bg-container {
  position: relative;
  overflow: hidden;
}

.page-bg-image {
  width: 100%;
  position: absolute;
  overflow: hidden;
  z-index: -2;
  > img {
    margin-left: 50%;
    transform: translate(-50%, 0);
  }
}

.lds-ring {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 100px;
  transform: translateX(-50%);
  width: 80px;
  height: 80px;

  div {
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border: 8px solid $mainColor;
    border-color: $mainColor transparent transparent transparent;

    &:nth-child(1) {
      animation-delay: -0.45s;
    }

    &:nth-child(2) {
      animation-delay: -0.3s;
    }

    &:nth-child(3) {
      animation-delay: -0.15s;
    }
  }
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body[class^="color-"] .content-section.transparent-color-box {
  div.teaser > div.richtext {
    padding: 12px;
  }

  div.container {
    border-width: 2px;
    border-style: solid;
    padding: 8px 24px 40px;
  }
}

// ==========================================================================
// Accessibility
// ==========================================================================

span, a, div, canvas, img, button, select {
  outline-color: $highlightColor;
  outline-width: 2px;
  outline-offset: 4px;
}

span:focus-visible, a:focus-visible, div:focus-visible, canvas:focus-visible, img:focus-visible, button:focus-visible, select:focus-visible {
  outline-style: solid;
}
