.masnahmen-detail {

  .detial-content{
    .content:last-child {
      margin-bottom: 0px;
      border-bottom: unset;
      padding-bottom: 0;
    }
    .content {
      position: relative;
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      cursor: pointer;
      border-bottom: 1px solid #80d2df;
      padding-bottom: 10px;

      a {
        position: relative;
        width: 100%;
        padding-left: 16px;
        padding-right: 80px;
        margin-bottom: 0px;
        padding-bottom: 0px;
        font-size: 1.125rem;
        hyphens: none;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 8px;
          height: 100%;
        }

        &.abgeschlossen {
          &::before {
            background-color: #00A5C0;
          }
        }
        &.geplant {
          &::before {
            background-image: url("../img/chart/blue_bar.svg");
          }
        }
        &.inumsetzung {
          &::before {
            background-image: url("../img/chart/green_bar.svg");
          }
        }

        i::before {
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          content: "\f285";
          //font-family: bootstrap-icons; set via bi class
        }
      }
    }
  }

  .legende {
    position: relative;
    display: flex;
    align-items: center;
    border-top: 1px solid #80D2DF;
    margin-top: 10px;

    .legende-content{
      position: relative;
      display: flex;
      align-items: center;
      margin-left: 10px;

      &:first-of-type {
        margin-left: 0;
      }

      span {
        position: relative;
        padding-left: 16px;
        margin-top: 6px;
        margin-bottom: 0px;
        padding-bottom: 0px;
        font-size: 0.75rem;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          width: 12px;
          height: 12px;
          top: 50%;
          transform: translateY(-50%);
        }

        &.abgeschlossen {
          &::before {
            background-color: #00A5C0;
          }
        }
        &.geplant {
          &::before {
            background-image: url("../img/chart/blue_bar.svg");
          }
        }
        &.inumsetzung {
          &::before {
            background-image: url("../img/chart/green_bar.svg");
          }
        }
      }

      svg {
        width: 10px;
        height: 10px;

        rect {
          width: 100%;
          height: 100%;
          fill: red;
        }
      }
    }
  }
}
