.videoslider {
    .more-link {
        text-align: center;

        a {
            position: relative;
            font-size: 1.25rem;
            display: inline-block;
            padding: 5px 20px;

            &::after {
                content: "";
                position: absolute;
                bottom: -3px;
                left: 0;
                display: block;
                width: 100%;
                height: 4px;
                background: $white;
            }
        }
    }
}