form {
    &.contact-form {
        input[type="text"],
        input[type="email"] {
            background-color: $form-grey;
            border-width: 0px;
            border-radius: 5px;

            &.captcha {
                border: 1px solid $black;
            }
        }

        textarea {
            background-color: $form-grey;
            border-width: 0px;
            border-radius: 5px;
        }

        .checkbox {
            input[type="checkbox"] {
                background-color: $form-grey;
                border-color: $form-grey;

                &:checked {
                    &::before {
                        color: $mainColor;
                    }
                }
            }

            a {
                text-decoration: underline;
            }
        }
    }

    a {
        color: $mainColor;
    }
}

// Form wrapper
.form {
    &.white-elements {
        input,
        textarea,
        select {
            background-color: $white !important;
        }
    }

    select {
        background-color: $form-grey;
        border: 1px solid $form-grey;
        border-width: 0px;
        border-radius: 4px;
        // -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    }

    .input-file {
        + label {
            background-color: $form-grey;
            border-radius: 3px;
            color: $mainColor;
            border: 2px solid $mainColor;
        }
    }

    .form-submit {
        .button {
            font-weight: normal;
            padding: 12px 20px;
            color: $mainColor;
            border: 2px solid $mainColor;
        }
    }
}