.comments {
    .post-comment {
        position: relative;
        margin-bottom: 30px;

        .comment-info {
            padding: 1em 0;
            padding-right: 150px;
        }

        .button-wrapper {
            text-align: right;
            padding: 0;

            .button {
                position: absolute;
                right: 0;
                top: 7px;
                font-weight: normal;
                padding: 4px 18px;

                @media screen and (max-width: $screen-s) {
                    position: relative;
                    top: auto;
                }
            }
        }

        .subcomment {
            position: relative;
            padding-left: 40px;

            @media screen and (max-width: $screen-s) {
                padding-left: 30px;
            }

            @media screen and (max-width: $screen-xs) {
                padding-left: 0;
            }
        }
    }
}
