// Override styles of tooltip plugin
.tippy-box {
    background-color: $white;
    color: $dark-grey;
    border: 1px solid $grey;

    &[data-placement^=top] {
        >.tippy-arrow {
            &::before {
                bottom: -8px;
            }
        }
    }

    .tooltip {
        display: block;
    }
}

.tooltip {
    display: none;

    &--feedback {
        span {
            display: inline-block;
            vertical-align: middle;
            margin: 0 10px 0 15px;
            font-size: 0.9375rem;
        }


        .feedback-progress {
            .progressbar {
                position: relative;
                margin: 0;
                background: $light-grey;
                border: 1px solid $grey;
                height: 20px;
                width: 180px;

                span {
                    position: absolute;
                    margin: 0;
                    left: 0;
                    top: 0;
                    height: 100%;
                    width: 0;
                    background: $yellow;
                }
            }
        }
    }
}