.teaser {
    position: relative;
  margin-bottom: 25px;

    @media screen and (max-width: $screen-l) {
        height: auto !important;
    }

    > a {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;

        :hover {
            cursor: pointer !important;
        }
    }

    .head {
        img {
            z-index: 1;
        }
    }

    .content {
        padding: 25px;
    }

    // Teaser - Richtext --> Text with image component
    &--text {
        &.image-right {
            .img-wrapper {
                &.col-12 {
                    @media screen and (max-width: $screen-md) {
                        margin-top: 30px;
                        margin-bottom: 0;
                    }
                }
            }
        }

        &-vertical {
            &.image-bottom {
                .img-wrapper {
                    margin-top: 30px;
                    margin-bottom: 0;
                }
            }
        }

        .img-wrapper {
            img {
                max-width: 100%;
                height: auto;
            }
        }
    }

    &--horizontal {
        margin-bottom: 25px;

        .row {
            margin-right: 0;
            margin-left: 0;
        }

        .head {
            padding: 25px;
            overflow: hidden;
            position: relative;
            text-align: center;

            img {
                width: auto;
                max-height: 125px;
            }
        }
    }

    &--wide {
        margin-bottom: 25px;

        h2 {
            margin-top: 15px;
            font-weight: 400;
            font-size: 1.875rem;
            line-height: 1.3;
            margin-bottom: 0;
        }

        .row {
            margin-left: 0;
            margin-right: 0;
        }

        .head {
            overflow: hidden;
            position: relative;
            text-align: center;

            img {
                width: auto;
                max-height: 125px;
            }
        }

        .content {
            .buttons {
                padding-top: 20px;
                display: flex;
                justify-content: center;

                .button-wrapper {
                    padding: 0;
                }
            }
        }
    }

    &--job {
        padding: 10px;
        margin-bottom: 15px;

        h3 {
            padding: 0;
            margin-bottom: 5px;
        }
    }

    &--search {
        margin-bottom: 60px;

        p.date {
            padding-bottom: 0;
        }

        h3 {
            position: relative;
            margin: 0;
            padding: 0;
            padding-bottom: 10px;
            font-size: 1.875rem;
            font-weight: normal;
            line-height: 1.3;

            a {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
            }

            .rating {
                padding-top: 10px;

                img {
                    width: 16px;
                    float: left;
                }

                span {
                    font-size: 0.875rem;
                    padding-left: 5px;
                    float: left;
                }
            }
        }
    }

    &--logo {
        .head {
            max-width: 280px;
            text-align: center;
            padding-top: 10px;
            margin-left: auto;
            margin-right: auto;

            img {
                width: 100%;
            }
        }

        .content {
            text-align: center;

            h3 {
                font-size: 1.4375rem;
                line-height: 1.3;
                font-weight: normal;
                margin-bottom: 0;
            }

            .button-wrapper {
                padding-top: 20px;
            }
        }
    }

    &--image {
        .head {
            padding-top: 56.25%;
            overflow: hidden;
            position: relative;

            img {
                position: absolute;
                top: 0;
                left: 50%;
                height: 100%;
                transform: translateX(-50%);
                width: auto;
            }
        }

        .content {
            h3 {
                display: block;
            }

            .hexalink {
                width: 36px;
                height: 40px;
                position: absolute;
                bottom: 24px;
                right: 24px;

                span {
                    line-height: 1.2;
                    color: $black;
                    font-size: 2rem;
                    text-indent: 1px;
                    position: relative;
                    top: -2px;
                }
            }
        }
    }

    &--hexagon {
        .hexagon {
            position: relative;
            padding-top: 100%;
            margin: 40px 0;

            &-bg-image {
                position: absolute;
                height: 100%;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }

            .text {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
                width: 85%;
                word-break: break-word;
            }
        }
    }
}
