form {
    &.comment-form {
        input[type="text"],
        input[type="email"] {
            max-width: 50%;

            @media screen and (max-width: $screen-s) {
                max-width: 100%;
            }
        }

        .form-submit {
            text-align: right;
            padding: 20px 0;
        }
    }

    &.contact-form {
        .form-row {
            &.two-col {
                >div:first-child {
                    @media screen and (max-width: $screen-md) {
                        margin-bottom: 30px;
                    }                    
                }
            }
        }

        .form-submit {
            text-align: right;
            padding: 40px 0;

            .button {
                margin-left: 20px;
                padding: 8px 20px;

                @media screen and (max-width: $screen-s) {
                    display: block;
                    width: auto;
                    margin-left: auto;
                    margin-top: 20px;
                }
            }
        }
    }

    // used for form Selects (multi checkboxes)
    .input-wrapper {
        &.horizontal {
            @media screen and (min-width: ($screen-s + 1)) {
                display: flex;
                justify-content: space-between;
            }
        }
    }

    label {
        &.captcha-label {
            display: inline-block;
            visibility: visible;
            font-size: 1.125rem;
            
            @media screen and (max-width: $screen-s) {
                vertical-align: middle;
            }
        }
        
        &.visuallyhidden {
            border: 0;
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            white-space: nowrap;
            width: 1px;
        }
    }

    .form-row {
        padding: 15px 0;
    }

    #mgnlhp {
        display: none;
    }
}

// Form wrapper
.form {
    h3 {
        margin-bottom: 0;
    }

    select {
        width: 100%;
        padding: 12px 15px;
    }

    .input-file {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;

        + label {
            font-size: 1rem;
            font-weight: normal;
            display: inline-block;
            visibility: visible;

            padding: 7px 10px;
            cursor: pointer;
            clip: auto;
            height: auto;
            margin: 0;
            position: relative;
            width: auto;
        }

        &:focus + label {
            outline: -webkit-focus-ring-color auto 1px;
        }
    }

    .form-submit {
        text-align: right;
        padding: 20px 0;
    }

    .error-wrapper {
        display: none;
        padding: 40px 15px;

        .empty-fields,
        .error-fields {
            &.hidden {
                display: none;
            }
        }

        .title {
            font-size: 1.875rem;
        }

        ul {
            list-style-type: disc;
            padding-left: 20px;
            margin-bottom: 30px;

            li {
                display: none;
                font-size: 1.125rem;

                &.visible {
                    display: block;
                }
            }
        }
    }

    &.has-error {
        .error-wrapper {
            display: block;
        }
    }
}