.row {
  &.teaser-collection {
    &.trans {
      .teaser {
        background: transparent;
        box-shadow: none;
      }
    }

    &.dark {
      .teaser {
        background-color: $dark-grey;

        p,
        h2,
        h3,
        a {
          color: $white;
        }
      }
    }

    &.white {
      .teaser {
        background-color: $white;

        p,
        h2,
        h3 {
          color: $black;
        }
      }
    }

    &.yellow {
      .teaser {
        background-color: $yellow;

        p,
        h2,
        h3,
        a {
          color: $black;
        }
      }
    }

    &.magenta {
      .teaser {
        background-color: $magenta;

        p,
        h2,
        h3,
        a {
          color: $white;
        }
      }
    }

    &.grey-gradient {
      .teaser {
        background-image: linear-gradient(180deg, $white 0%, $light-grey 100%);

        p,
        h2,
        h3,
        a {
          color: $black;
        }
      }
    }

    &.blue-gradient {
      .teaser {
        background-image: linear-gradient(180deg, $mainColor 0%, $secondColor 100%);

        p,
        h2,
        h3,
        a {
          color: $white;
        }
      }
    }
  }

  @media screen and (max-width: ($screen-md - 1)) { // to medium
    .col-2 .teaser .hexagon .text {
      font-size: 0.4rem;
    }
    .col-3 .teaser .hexagon .text {
      font-size: 0.6rem;
    }
    .col-4 .teaser .hexagon .text {
      font-size: 1rem;
    }
    .col-6 .teaser .hexagon .text {
      font-size: 1rem;
    }
    .col-12 .teaser .hexagon .text {
      font-size: 3rem;
    }
  }

  @media screen and (min-width: ($screen-md + 1)) and (max-width: ($screen-l - 1)) { // from medium
    .col-md-2 .teaser .hexagon .text {
      font-size: 0.6rem;
    }

    .col-md-12 .teaser .hexagon .text {
      font-size: 3.5rem;
    }

    .col-md-6 .teaser .hexagon .text {
      font-size: 1.8rem;
    }

    .col-md-4 .teaser .hexagon .text {
      font-size: 1.2rem;
    }

    .col-md-3 .teaser .hexagon .text {
      font-size: 0.8rem;
    }
  }

  @media screen and (min-width: ($screen-l + 1)) {
    .col-lg-2 .teaser .hexagon .text {
      font-size: 0.8rem;
    }

    .col-lg-3 .teaser .hexagon .text {
      font-size: 1rem;
    }

    .col-lg-4 .teaser .hexagon .text {
      font-size: 1.4rem;
    }

    .col-lg-12 .teaser .hexagon .text {
      font-size: 4rem;
    }

    .col-lg-6 .teaser .hexagon .text {
      font-size: 2rem;
    }
  }
}

.teaser {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  background-color: $white;

  // General Styles for all teaser variants
  .richtext {
    a {
      color: $mainColor;
    }
  }

  // Teaser - Richtext --> Text with image component
  &--text {
    box-shadow: none;
    background-color: transparent;

    &.has-border {
      background-color: $white;
      padding: 25px;
      box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
      margin-bottom: 25px;

      p {
        padding: 0;
      }

      .richtext {
        a {
          color: $mainColor !important;
        }
      }
    }
  }

  &--wide {
    background-color: transparent;
    box-shadow: none;
    border-bottom: 1px dotted $black;

    h2 {
      a {
        color: $mainColor;
      }
    }
  }

  &--job {
    background: $light-grey;
    color: #000000;
    box-shadow: none;
    border: solid 1px $mainColor;

    &:hover {
      border: 1px solid $light-grey;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    }

    h3 {
      a {
        font-size: 1.125rem;
        font-weight: 600;
        color: $mainColor;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    p {
      font-weight: bold;
      color: $dark-grey;
      font-size: 0.875rem;
      margin-bottom: 5px;
      padding: 0;
    }
  }

  &--search {
    background-color: transparent;
    border: 0;
    box-shadow: none;

    p.date {
      font-size: 1.125rem;
    }

    h3 {
      color: $mainColor;
    }

    .highlight {
      color: $white !important;
      padding: 5px !important;
      font-weight: bold !important;
      background-color: $mainColor !important;
    }
  }

  &--image {
    .content {
      padding-bottom: 64px;

      .hexalink {
        background-image: url("../img/hexagon-tuerkis.svg");
        background-size: contain;
        background-repeat: no-repeat;

        span {
          color: $white;
        }
      }
    }
  }

  &--hexagon {
    background-color: transparent;
    border: 0;
    box-shadow: none;

    .hexagon {
      .text {
        font-weight: bold;
        font-size: 2.5rem;
        padding: 1em;
      }
    }
  }
}
