.iframe,
.video,
.videoplaylist {
    margin-bottom: 25px;

    h2 {
        margin-bottom: 0;
    }

    .iframe-wrapper,
    .video-wrapper {
        padding-top: 56.25% !important;
        position: relative;

        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    .videoslider {
        .swiper {
            margin: 50px 90px;

            .swiper-slide {
                .video-preview-wrapper {
                    text-align: center;
                }
                
                a {
                    &:hover {
                        cursor: pointer;
                        
                        &::before {
                            background: url("../img/playhover.png");
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 20%;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            display: inline-block;
                            z-index: 10;
                            content: "";
                        }
                    }
                }
                
                &.active {
                    a {
                        &::before {
                            background: url("../img/playhover.png");
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 20%;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            display: inline-block;
                            z-index: 10;
                            content: "";
                        }
                    }
                }
            }
        }
    }
}