.author-section {
    min-height: 122px;

    img.author-image {
        max-width: 250px;
        position: absolute;
        right: 0;
        top: 0;
    }

    .social-media {
        margin-bottom: 55px;

        a {
            margin-right: 15px;

            i {
                font-size: 1.5625rem;
            }
        }
    }

    .collapse-content {
        .author-link {
            text-align: center;
            font-size: 1.125rem;

            img {
                width: 100px;
                margin: 0 auto;
                display: block;
            }

            span {
                display: block;
                width: 100%;
                padding: 20px;
            }
        }
    }
}