.link-list {
    &.block {
        span {
            display: block;
        }
    }
    &.inline {
        &.center {
            ul {
                text-align: center;
            }
        }

        li {
            display: inline-block;
            padding-right: 15px;
            margin-bottom: 5px;
        }
    }

    &.bullets {
        ul {
            list-style: disc;
        }
    }

    &.ordered {
        ul {
            list-style: decimal;
        }
    }
}
