.stageheader {
    // Text or Image Stageheader
    &--text-image {
        .swiper {
            .swiper-wrapper {
                .swiper-slide {
                    .swiper-slide--container {
                        .swiper-slide--content {
                            .content-heading {
                                h1 {
                                    line-height: 1.2;
                                    font-size: 3.75rem;
                                    font-weight: bold;
                                    text-transform: uppercase;
                                    color: $white;
                                    margin: 0;
                                    padding: 0;

                                    @media screen and (max-width: $screen-md) {
                                        font-size: 3.4375rem;
                                    }

                                    @media screen and (max-width: $screen-s) {
                                        font-size: 2.75rem;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &--slider {
        .swiper {
            .swiper-wrapper {
                .swiper-slide {
                    &--container {
                        &.magenta {
                            .button-wrapper.more-link .button::after {
                                background: $magenta;
                            }

                            .content-heading {
                                a::after {
                                    background: $magenta;
                                }
                            }
                        }

                        &.yellow {
                            .button-wrapper.more-link .button::after {
                                background: $yellow;
                            }

                            .content-heading {
                                a::after {
                                    background: $yellow;
                                }
                            }
                        }
                    }

                    &--content {
                        .content-heading {
                            margin-bottom: 40px;

                            a {
                                font-size: 2rem;
                                display: inline-block;
                                line-height: 1.1;

                                &::after {
                                    content: "";
                                    margin: 15px auto 5px;
                                    width: 75%;
                                    display: block;
                                    height: 5px;
                                    background: $secondColor;
                                    background: linear-gradient(to right, $secondColor 1%, $mainColor 100%);
                                }

                                &.yellow::after {
                                    background: $yellow;
                                }

                                &.black::after {
                                    background: $light-black;
                                }

                                &.magenta::after {
                                    background: $magenta;
                                }
                            }
                        }

                        .content-body {

                          span {
                              font-size: 1.6875rem;
                              font-weight: 400;
                              display: block;
                              line-height: 1.2;
                              padding-bottom: 20px;
                          }
                        }
                    }

                    // Honeycomb Background Image
                    .honeycomb {
                        max-width: 800px;
                    }

                    // Arrow button to scroll down the page
                    .scrolldown {
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 10px;
                        display: block;
                        height: 75px;
                        width: 65px;
                        opacity: 0.7;
                        background-image: url("../img/hexagon-weiss-pfeil-unten.svg");
                        background-size: contain;

                        &:hover {
                            opacity: 0.5;
                        }

                        &::after {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

// Mobile Layout
@media screen and (max-width: $screen-s) {
    .stageheader {
        &--slider {
            .swiper {
                .swiper-wrapper {
                    .swiper-slide {
                        &--content {
                            margin: 150px auto 50px;
                            transform: scale(0.8);
                        }

                        .honeycomb {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $screen-xs) {
    .stageheader {
        &--slider {
            .swiper {
                .swiper-wrapper {
                    .swiper-slide {
                        &--content {
                            transform: scale(1);
                            margin: 80px 25px 120px;
                            height: auto;
                            padding: 100px 0;

                            .content-body {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}
